import {Component, ElementRef, Input, OnInit, ViewChild} from '@angular/core';
import {EventListener} from '@angular/core/src/debug/debug_node';

@Component({
  selector: 'app-search-filter',
  templateUrl: './search-filter.component.html',
  styleUrls: ['./search-filter.component.css']
})
export class SearchFilterComponent implements OnInit {
  @Input() item;
  @Input() styleSelct: string;
  public setValueSelect: string;
  @ViewChild('items') items: ElementRef;

  constructor() { }

  ngOnInit() {
    this.defoultSelect();
  }

  toggleSelect() {
    this.items.nativeElement.hidden = !this.items.nativeElement.hidden;
  }
  closeSelect () {
    this.items.nativeElement.hidden = true;
  }
  getValueSelect(e) {
    console.dir(e.target.innerText);
    this.setValueSelect = e.target.innerText;
    this.closeSelect();
  }
  defoultSelect () {
    this.item.find(el => this.setValueSelect = el.title);
  }

}
