  public classTab = 'first';
  public adviceTabTitle: string;
  public adviceTabText: string;

  public modalIsOpen: boolean;

  public countSlids: number;
  public sldeModalImg: string;
  public currentslide = 1;



  constructor( private title: Title, private meta: Meta) {
    title.setTitle('  -  ');
    meta.addTags([
      {name: 'key', content: ', '},
      {name: 'description', content: ' '}
    ]);
  }

  ngOnInit() {
    this.onTabContent( 'first');
    this.modalIsOpen = false;
  }

  // Toggle tabs
  onTab(e) {
    if ( e === 'first') {
      this.classTab = e;
    }
    if ( e === 'second') {
      this.classTab = e;
    }
    if ( e === 'third') {
      this.classTab = e;
    }
    this.onTabContent(e);
  }

  onTabContent (e) {
    this.adviceTab.filter( (tab: any) => {
      tab.styleOption === e ? this.adviceTabTitle = tab.title : tab;
      tab.styleOption === e ? this.adviceTabText = tab.description : tab;
    });
  }

  // Open modal
  openModal () {
    console.log('click');
    this.modalIsOpen = !this.modalIsOpen;
    this.nextSlide(1);
    this.modalIsOpen ? document.body.style.overflow = 'hidden' : document.body.style.overflow = 'initial' ;
  }

  // Slider in modal
  sliderModal (slide) {
    this.bach.filter( ( el, i ) => {
      this.countSlids = i + 1;
      // i + 1 === slide ? this.sldeModalImg = el.imgPath : el;
      if ( i + 1 === slide ) {
        this.sldeModalImg = el.imgBatchPath;
      }
    });
  }

  nextSlide (e) {
    this.currentslide = this.currentslide + e;

    if ( this.currentslide > this.countSlids ) {
      this.currentslide = 1;
    }

    if ( this.currentslide < 1 ) {
      this.currentslide = this.countSlids;
    }

    this.sliderModal (this.currentslide);

  }

