  constructor() { }

  ngOnInit() {
  }
  htmlCollToObj (e) {
    return [].slice.call(e);
  }
  openList(e) {
    this.closesList();
    if ( e.target.parentElement.matches('.item-btn') ) {
      e.toElement.parentElement.nextElementSibling.classList.add('is-open');
    }
  }
  closesList () {
    this.htmlCollToObj(this.category.nativeElement.children).filter( el => el.children[1].classList.remove('is-open'));
  }

